<template>
  <div>
    <Card v-if="!showEdit" dis-hover :bordered="false">
      <Alert show-icon>
        路由说明：
        <template slot="desc">
          <p>路由匹配：根据请求的Label数据进行路由匹配，匹配到的路由通过关联的通知通道的各个接入点进行通知</p>
          <p>路由条件：卡片之间为或关系，卡片内的条件为且关系</p>
        </template>
      </Alert>
      <div class="search-con search-con-top">
        <Button type="primary" class="search-btn" @click="handleAdd">创建通知路由</Button>
        <Button class="search-btn" :disabled="i.btnDisabled" @click="handleOpt('no')">禁用</Button>
        <Button class="search-btn" :disabled="i.btnDisabled" @click="handleOpt('yes')">启用</Button>
        <Select v-model="filter_map.status" style="width:80px" clearable filterable placeholder="状态"
          @on-change="handleSearch">
          <Option v-for="item in stateList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
        <Input class="search-input" v-model="initPara.searchText" clearable search @on-search="handleSearch"
          :maxlength='80' placeholder="输入关键字搜索" />

        <Button style="float: right; margin-right: 2px" icon="md-refresh" :loading="i.btnLoading"
          @click="handleRefresh"></Button>

      </div>
      <Table ref="selection" @on-sort-change="handleSort" @on-selection-change="handleSelect" :columns="columns"
        :data="tableData" :loading="i.tableLoading"></Table>
      <div style="margin: 10px; overflow: hidden">
        <div style="float: right;">
          <Page :total="i.pageTotal" :current="initPara.pageNum" :page-size="initPara.pageSize" show-total show-sizer
            show-elevator :page-size-opts="[10, 15, 50, 1000]" @on-change="handlerChangePage"
            @on-page-size-change="handlePageSize" />
        </div>
      </div>

    </Card>
    <route-edit v-else :formData="theData" :channalList="channalList" @save-data="handleSaveData"
      @edit-close="handleClose" />

  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import { getRoute, optRoute } from '@/api/notice/noc-route'
import { getChannel } from '@/api/notice/channel'
import routeEdit from './components/route-edit'
import formatDate from '@/libs/util.js'
export default {
  components: { routeEdit },
  data () {
    return {
      channalList: [],
      showEdit: false,
      action: 'post',
      //
      initPara: {
        pageNum: 1, // 当前页码
        pageSize: 10,  // 每页条数
        searchText: "",//查找值
        field: "",      //展示字段
        order: "descend", //排序
        searchField: "",  // 查找字段
        custom: "all",
        cache: "yes",// 缓存
      },
      filter_map: {
      },
      initForm: {
        status: 'yes',
        condition_list: [
          [{
            label: '',
            operator: '==',
            value: '',
            index: 1,
            status: 1
          }],

        ],
      },
      i: {
        pageTotal: 0, // 数据总数
        tableLoading: true,
        btnDisabled: true,
        btnLoading: false
      },
      selectList: [],
      theData: {},
      stateList: [{ value: 'yes', label: '正常' }, { value: 'no', label: '已禁用' }],
      columns: [
        {
          type: 'selection',
          key: '',
          width: 60
        },
        {
          title: '名称',
          key: 'name',
          minWidth: 160
        },
        {
          title: '状态',
          key: 'status',
          sortable: true,
          minWidth: 110,
          maxWidth: 150,
          render: (h, params) => {
            const status = params.row.status
            const miaoshu = status === 'yes' ? '正常' : '禁用'

            return h('div', [
              h('Badge', {
                props: {
                  status: status === 'yes' ? 'success' : 'default'
                }
              }),
              h('span', ' ' + miaoshu)
            ]);
          }
        },

        {
          title: '通知通道',
          key: 'channel_name',
          sortable: true,
          minWidth: 120
        },
        {
          title: '更新人',
          key: 'updated_by',
          sortable: true,
          minWidth: 105,
          maxWidth: 200
        },
        {
          title: '更新时间',
          key: 'updated_at',
          minWidth: 165,
          maxWidth: 200,
          render: (h, params) => {
            return h("div", formatDate(params.row.updated_at));
          },
        },
        {
          title: '操作',
          key: '',
          minWidth: 135,
          maxWidth: 250,
          render: (h, params) => {
            return h('div', [
              h('a', {
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    this.handleEdit(params.row)
                  }
                }
              }, '编辑'
              ),
              h('a', {
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => { this.handleClone(params.row) }
                }
              }, '克隆'
              ),
              h('a', {
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => { this.handleDel(params.row) }
                }
              }, '删除'
              ),
              //
            ])
          }
        }
        //
      ],
      tableData: []
    }
  },
  methods: {
    async handleGetData () {
      // this.i.btnLoading = true
      this.initPara['filter_map'] = this.filter_map
      let res = await getRoute(this.initPara)
      if (res.data.code === 0) {
        this.tableData = res.data.result.data;
        this.i.pageTotal = res.data.result.count;
      } else {
        this.$Message.error(res.data.message)
      }
      this.i.tableLoading = false
      this.initPara.cache = 'yes'
      setTimeout(() => {
        this.i.btnLoading = false
      }, 1000);
    },
    async handleGetChannel () {
      let res = await getChannel({ pageNum: 1, pageSize: 10000 })
      if (res.data.code === 0) {
        this.channalList = res.data.result.data;
      } else {
        this.$Message.error(res.data.message)
      }
    },
    handleClose () {
      this.showEdit = false
      this.handleGetData()
      this.selectList = []
    },
    handleAdd () {
      this.action = 'post'
      this.showEdit = true
      this.theData = Object.assign(this.$options.data().theData, this.initForm)
    },
    handleEdit (paramsRow) {
      this.action = 'put'
      this.showEdit = true
      this.theData = paramsRow
    },
    handleClone (paramsRow) {
      this.action = 'post'
      this.showEdit = true
      delete paramsRow.id
      paramsRow.name = `${paramsRow.name}-副本`
      this.theData = paramsRow
    },
    //Disable enable
    async handleOpt (opt) {
      this.$Modal.confirm({
        title: '提醒',
        content: opt == 'no' ? `<p>确定禁用选中的路由吗？，请谨慎操作！</p>` : `<p>确定启用选中的通知路由吗？</p>`,
        onOk: () => {
          setTimeout(async () => {
            let res = await optRoute({ ids: this.selectList, status: opt }, 'patch')
            if (res.data.code === 0) {
              this.handleClose()
            } else {
              this.$Message.error('')
            }
          }, 1000);
        },
        onCancel: () => { this.$Message.info('回头是岸~') }
      });
    },
    //
    //
    handleSaveData (saveData) {
      optRoute(saveData, this.action).then(res => {
        if (res.data.code == 0) {
          this.$Message.success('成功')
          this.handleClose()
        } else {
          this.$Message.error(res.data.message)
        }
      })
    },
    //
    handleDel (paramsRow) {
      this.$Modal.confirm({
        title: '提醒',
        content: `<p>删除《${paramsRow.name}》之后无法找回，请谨慎操作！</p>`,
        onOk: () => {
          setTimeout(() => {
            optRoute({ ids: [paramsRow.id] }, 'delete').then(res => {
              if (res.data.code === 0) {
                this.handleGetData()
                this.$Message.success(res.data.message)
              } else {
                this.$Message.error('删除失败')
              }
            })
          }, 1000);
        },
        onCancel: () => { this.$Message.info('回头是岸~') }
      });
    },
    //
    handleRefresh () {
      this.initPara.pageNum = 1
      this.initPara.cache = 'no'
      this.handleGetData()
    },
    handleSearch () {
      this.initPara.pageNum = 1
      this.handleGetData()
    },
    handleSelect (val) {
      let selectIdList = []
      val.forEach(item => {
        selectIdList.push(item.id)
      })
      this.selectList = selectIdList
    },
    handleSort (column, key, order) {
      this.initPara.order = column.order === "asc" ? 'ascend' : 'descend'
      this.initPara.field = column.key
      this.handleGetData()
    },
    handlerChangePage (value) {
      this.initPara.pageNum = value
      this.handleGetData()
    },
    handlePageSize (value) {
      this.initPara.pageNum = 1
      this.initPara.pageSize = value
      this.handleGetData()
    },
    //
    ...mapMutations([
      'setShowBusiness'
    ]),

  },
  watch: {
    selectList (val) {
      if (val.length > 0) {
        this.i.btnDisabled = false
      } else {
        this.i.btnDisabled = true
      }
    },
    resource_group (val) {
      this.initPara.pageNum = 1
      this.handleGetData()
    },

  },
  computed: {
    ...mapState({
      rules: state => state.user.rules,
      resource_group: state => state.app.resource_group,
      businessList: state => state.app.businessData // 业务列表
    })
  },
  mounted () {
    this.handleGetData()
    this.handleGetChannel()
    this.setShowBusiness(false)
  },
  beforeRouteLeave (to, from, next) {
    this.setShowBusiness(true)
    next()
  }
  //
}
</script>
<style lang="less" scoped>
.search-con {
  padding: 10px 0;

  .search {
    &-col {
      display: inline-block;
      width: 200px;
    }

    &-input {
      display: inline-block;
      width: 400px;
      margin-left: 5px;
    }

    &-btn {
      display: inline-block;
      margin-left: 2px;
      margin-right: 5px;
    }

    &-add-btn {
      margin-right: 10px;
    }
  }
}
</style>
